nav {
    padding: 0 1rem;
}

.content{
    min-height:  calc(100vh - 70px - 64px);
    padding: 1.5rem 0;
}

.movies{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
    gap: 1rem;
}

.search-btn{
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
}

label:not(:last-child) {
    margin-right: 1rem ;
}